.chat_input {
  position: sticky;
  bottom: 0px;
  height: 75px;
  background-color: var(--chat-bg);
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(7px);
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  z-index: 2000;
  .input_field {
    position: relative;
    display: flex;
    max-width: 85%;
    width: 100%;
    textarea {
      width: 100%;
      color: var(--text-field-clr);
      background-color: var(--text-field-bg);
      border: 1px solid var(--text-field-border);
      padding: 10px 40px 10px 15px;
      height: 50px;
      border-radius: 8px;
      resize: none;
      outline: none;
      font-family: inherit;
      font-size: 16px;
      &::-webkit-scrollbar {
        width: 7px;
      }
      &::-webkit-scrollbar-track {
        background: white;
      }
      &::-webkit-scrollbar-thumb {
        background: lightgray;
        border-radius: 10px;
      }
    }
    i {
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
    }
  }

  i {
    font-size: 26px;
    color: #9498a3;
    cursor: pointer;
  }
  button {
    width: 50px;
    border: none;
    background-color: #4eac6e;
    border-radius: 4px;
    padding: 10px 0px;
    i {
      color: white;
      font-size: 24px;
    }
  }
}
